<template>
  <div id="ccHead">
    记事本
    <i class="glyphicon glyphicon-plus"  style="margin-left: 20px" @click="addFile"></i>
  </div>
</template>

<script>
  export default {
    methods: {
      addFile:function(){
        this.$store.commit('add_file');
      }
    }
  }
</script>

<style>
   #ccHead{
     background-color: #0d3349;
     color:whitesmoke;
     height:50px;
     font-size:20px;
     line-height:50px;
     padding-left:25px;
   }
  #addFile
  {
    height:50px;
    line-height:50px;
    float:right;
    margin-right:100px;
  }
</style>



